import React, {PureComponent} from 'react'
import {
  Modal,
  Table,
  Row,
  Col
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.rawConveProduce, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
      
    }
    this.columns = [{
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '商品条码',
      width: 150,
      dataIndex: 'productBarCode',
      key: 'productBarCode'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '类型',
      width: 100,
      dataIndex: 'materialType',
      key: 'materialType'
    }, {
      title: '库区',
      width: 100,
      dataIndex: 'reservoirAreaCode',
      key: 'reservoirAreaCode'
    }, {
      title: '储位',
      width: 100,
      dataIndex: 'storeLocationCode',
      key: 'storeLocationCode'
    }, {
      title: '数量',
      width: 100,
      dataIndex: 'qty',
      key: 'qty'
    }, {
      title: '单价（元）',
      width: 100,
      dataIndex: 'unitPriceNoTax',
      key: 'unitPriceNoTax'
    }, {
      title: '总金额（元）',
      width: 150,
      dataIndex: 'totalPriceNoTax',
      key: 'totalPriceNoTax'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetDetailRecord, match: {params}, history} = this.props
    handleGetDetailRecord({searchParams: {orderCode: [params.id]}})

  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }

  render() {
    const {
      recordLoading, 
      searchResultRecord,
      recordForm,
      shipperName,
      remark, 
    } = this.props
    console.log(recordForm)
    return (
        <div className='fullOrderDel'>
          <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
          <div style={{paddingLeft: 20}}>
            <Row>
              <Col span={12}><h4>货主：{recordForm?recordForm.shipperName:null}</h4></Col>
              <Col span={12}><h4>备注：{recordForm?recordForm.remark:null}</h4></Col>
            </Row>
          </div>
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={record => record.id}
              loading={recordLoading}
              columns={this.columns}
              scroll={{x: 900, y: 400}}
              dataSource={searchResultRecord}
              pagination={false}
            />
          </div>
        </div>
    )
  }
}



