// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.RAWCONVEPRODUCE_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }

  getData({api: API_MATERIALPRODUCTHEAD_PAGELIST, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.RAWCONVEPRODUCE_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.RAWCONVEPRODUCE_FILE
    })
  })
}


// //原料转成品  详情
export const handleGetDetailRecord = (reqData) => (dispatch) => {
  dispatch({type: actionType.RAWCONVEPRODUCE_UPDATE, data: {recordLoading: true}})
  getData({api: API_MATERIALPRODUCTITEM_GETITEMINFO, ...reqData}).then(data => {
    // 相应的逻辑判断
    // let n = []
    // n.push(data.result)
    dispatch({
      type: actionType.RAWCONVEPRODUCE_UPDATE,
      data: {
        recordLoading: false,
        searchResultRecord: data.result.items,
        recordForm: data.result.header
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.RAWCONVEPRODUCE_FILE
    })
  })
}

